@font-face {
  font-weight: 500;
  font-family: "Poppins";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Poppins";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Cormorant Garamond";
  font-style: italic;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-bold-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold-italic.woff") format("woff"); }

html {
  scroll-behavior: smooth;
  height: 100%; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  color: #1d1613;
  background-color: #ffffff;
  height: 100%; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  background-position: 0 0; }

.main-page__wrapper {
  margin-top: 30px;
  padding: 0 40px;
  max-width: 1100px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.main-page__wrapper h1 {
  margin: 0;
  font-size: 42px;
  line-height: 63px;
  text-align: center; }

.main-page__add-task {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  margin-top: 30px; }

.main-page__add-task input {
  font-size: 20px;
  line-height: 30px;
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  border: 1px solid #000000;
  cursor: pointer; }
  .main-page__add-task input:hover {
    background-color: #ffc0cb; }

.main-page__add-task button {
  margin-left: 20px; }

.main-page__new-task-btn {
  width: 40px;
  padding: 0;
  height: auto;
  background-color: #ffffff;
  border: none;
  cursor: pointer; }

.main-page__clear-task-btn {
  width: 40px;
  padding: 0;
  height: auto;
  border: none;
  background-color: #ffffff;
  cursor: pointer; }
  .main-page__clear-task-btn:hover {
    background-color: #ffc0cb; }

.main-page__list {
  margin: 0;
  padding: 0;
  margin-top: 40px; }

.list {
  list-style: none; }

.list li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px; }

.list li:first-child {
  margin-top: 0; }

.list p {
  margin: 0;
  margin-left: 5px; }

.list__check-wrapper {
  width: 40px;
  height: 40px;
  margin-right: 10px; }

.list__check-wrapper label {
  position: relative;
  width: 40px;
  height: 100%; }

.list__check-wrapper input:not(:checked) + label::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  top: 0;
  left: 0;
  background-image: url("../../img/circle.svg");
  background-repeat: no-repeat;
  background-size: 40px 40px;
  background-position: center center;
  background-color: transparent;
  cursor: pointer; }

.list__check-wrapper input:checked + label::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  top: 0;
  left: 0;
  background-image: url("../../img/check-circle.svg");
  background-repeat: no-repeat;
  background-size: 40px 40px;
  background-position: center center; }

.list__check-wrapper span {
  margin-left: 15px; }

.list button {
  position: relative;
  width: auto;
  height: 20px;
  background-color: #ffffff;
  border: 1px solid #000000;
  cursor: pointer; }
  .list button:hover {
    background-color: #ffc0cb; }
    .list button:hover::after, .list button:hover::before {
      background-color: #292929; }
